import React from "react";
import "./card.css";
import { Card } from "react-bootstrap";
import useIntersectionObserver from "./observer.js";
import Image from "./image";
import { makeStyles } from "@material-ui/core/styles";
import clsx from "clsx";
import CardHeader from "@material-ui/core/CardHeader";
import CardMedia from "@material-ui/core/CardMedia";
import CardContent from "@material-ui/core/CardContent";
import CardActions from "@material-ui/core/CardActions";
import Collapse from "@material-ui/core/Collapse";
import Avatar from "@material-ui/core/Avatar";
import IconButton from "@material-ui/core/IconButton";
import Typography from "@material-ui/core/Typography";
import { red } from "@material-ui/core/colors";
import FavoriteIcon from "@material-ui/icons/Favorite";
import ShareIcon from "@material-ui/icons/Share";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import MoreVertIcon from "@material-ui/icons/MoreVert";
import GetAppIcon from "@material-ui/icons/GetApp";

const useStyles = makeStyles((theme) => ({
  root: {
    maxWidth: 445,
  },
  media: {
    height: 0,
    // paddingTop: "56.25%", // 16:9
  },

  avatar: {
    backgroundColor: red[500],
  },

  paddingless: {
    padding: 0,
    marginLeft: "5px",
  },

  icon: {
    color: "white",
    marginLeft: "10px",
  },
}));

const CardComponet = (props) => {
  const classes = useStyles();

  const { item } = props;
  const ref = React.useRef();

  const [isVisible, setIsVisible] = React.useState(false);

  useIntersectionObserver({
    target: ref,
    onIntersect: ([{ isIntersecting }], observerElement) => {
      if (isIntersecting) {
        setIsVisible(true);
        observerElement.unobserve(ref.current);
      }
    },
  });

  function downloadEmployeeData(imgurl, imgname) {
    fetch(imgurl).then((response) => {
      response.blob({ type: "image/png" }).then((blob) => {
        let url = window.URL.createObjectURL(blob);
        let a = document.createElement("a");
        a.href = url;
        a.download = imgname + ".png";
        a.click();
      });
      //window.location.href = response.url;
    });
  }

  async function handleDownload(imgurl) {
    console.log(imgurl);
    let a = document.createElement("a");
    a.href = imgurl;
    a.download;
    a.click();
  }

  return (
    <React.Fragment>
      <Card
        className={classes.root}
        bg="danger"
        style={{
          backgroundColor: "rgba(2,4,43,0.4)",
          width: "100%",
          // borderColor: "rgba(2,2,2,0.4)",
          borderWidth: "2px",
          top: "10px",
          color: "white",
          marginTop: "30px",
          overflow: "hidden",
          // paddingTop: "10px",
          // borderRadius: "5px",
          borderBottomLeftRadius: "5px",
          borderBottomRightRadius: "5px",
          borderTopLeftRadius: "10px",
          borderTopRightRadius: "10px",
          alignContent: "center",
        }}
      >
        <CardHeader
          className="p-2"
          avatar={
            <Avatar aria-label="name_logo" className={classes.avatar}>
              {item["user_name"][0]}
            </Avatar>
          }
          title={item["user_name"]}
          subheader={item["upload_date"]}
        />
        <Card.Body
          // className="cardBody"
          style={{
            padding: 0,
            paddingTop: "0px",
            backgroundColor: "rgba(255,255,255,1)",
          }}
        >
          <div ref={ref}>
            {isVisible && (
              <Image
                src={"https://chaitanya360.pythonanywhere.com/" + item["img"]}
                style={{
                  borderRadius: "0px",
                  borderTopLeftRadius: "10px",
                  borderTopRightRadius: "10px",
                  borderBottom: "1px solid grey",
                  maxHeight: "800px",
                  minHeight: "0px",
                  position: "relative",
                  display: "inline",
                  overflow: "hidden",
                }}
              ></Image>
            )}
          </div>
          {/* <Card.Title
            style={{
              paddingTop: "25px",
              margin: 0,
              paddingBottom: "20px",
              textDecoration: "none",
              backgroundColor: "rgba(255,255,255,0.4)",
              fontFamily: "sans-serif",
            }}
          >
            {item["title"]}
          </Card.Title>
          <Card.Text
            style={{
              color: "black",
              textAlign: "left",
              textDecoration: "none",
              paddingBottom: "30px",
              overflowWrap: "break-word",
              paddingRight: "0px",
              paddingLeft: "5px",
              marginBottom: "0px",
              fontSize: "1em",
            }}
          >
            {item["description"]}
            <br />
            <br />
            {item["content"] == null
              ? " "
              : item["content"].substring(0, item["content"].indexOf("[+"))}
          </Card.Text> */}
        </Card.Body>

        <Card.Footer
          // style={{ padding: "0px", backgroundColor: "black" }}
          className="p-2"
        >
          <FavoriteIcon className={classes.icon + " icon"} />
          <ShareIcon className={classes.icon + " icon"} />
          <GetAppIcon
            className={classes.icon + " icon"}
            style={{ float: "right", fontSize: 25 }}
            onClick={() =>
              downloadEmployeeData(
                "https://chaitanya360.pythonanywhere.com/" + item["img"],
                item["img"]
              )
            }
          />
        </Card.Footer>
      </Card>
    </React.Fragment>
  );
};

export default CardComponet;
